import React from "react";

import { Container, Title, Body, InputField, Button, Form } from "./elements";

// To DO (Abiel 2019-02-23): Remove redirection to Google's "thank you for submitting"

const ContactForm = ({
  title,
  body,
  buttonTitle,
  formUrl,
  inputName,
  onDarkBackground,
}) => {
  const textColor = onDarkBackground ? "#FFF" : "#000";
  return (
    <Container>
      {title && <Title color={textColor}>{title}</Title>}
      <Body color={textColor}>{body}</Body>
      <Form action={formUrl} target="_blank">
        <InputField
          placeholder="Email"
          type="email"
          autoComplete="off"
          name={inputName}
        />
        <Button type="submit">{buttonTitle}</Button>
      </Form>
    </Container>
  );
};

export default ContactForm;
